<template>
	<div id="wrapper">
		<div id="head">
			<span @click="back">
				<img src="../../static/arrleft.png" /></span>
			<div>
				配送范围
			</div>
			<span></span>
		</div>
		<div id="main">

		</div>
	</div>
</template>

<script>
	import AMap from 'AMap'
export default {
  name: 'peisongfanwei',
  data () {
    return {

    }
  },
  mounted(){
	  var that=this
	  // 查询店铺信息
	  	$.ajax({
	  		type: "post",
	  		url: that.myurl03 + "/user/getUsTraderStoresByUsTraderStoresId ",
	  		dataType: 'json',
	  		data: {
	  			usTraderStoresId: that.usTraderStoresId
	  		},
	  		success: function(res) {
	  			var map = new AMap.Map('main', {
	  				zoom: 15,
	  				center: [res.data.usTsCoordinatesLongitude, res.data.usTsCoordinatesDimension]
	  			})
	  			var marker = new AMap.Marker({
	  				position: new AMap.LngLat(res.data.usTsCoordinatesLongitude, res.data.usTsCoordinatesDimension),   // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
	  				title: '北京'
	  			});

	  			// 将创建的点标记添加到已有的地图实例：
	  			map.add(marker);
	  			var circle = new AMap.Circle({
	  			    center: new AMap.LngLat(res.data.usTsCoordinatesLongitude, res.data.usTsCoordinatesDimension), // 圆心位置
	  			    radius: res.data.usTsScrope*100,  //半径
	  			    strokeColor: "#F33",  //线颜色
	  			    strokeOpacity: 1,  //线透明度
	  			    strokeWeight: 3,  //线粗细度
	  			    fillColor: "#ee2200",  //填充颜色
	  			    fillOpacity: 0.35 //填充透明度
	  			});
	  			 map.add(circle);
	  			map.setFitView();
	  		}
	  	});
  },
  methods:{
		back:function(){
			this.$router.back()
		}
  },
  computed: {
  	myurl() {
  		return this.$store.state.myurl
  	},
  	myurl03() {
  		return this.$store.state.myurl03
  	},
  	usTraderStoresId() {
  		return this.$store.state.usTraderStoresId
  	}
  }
}
</script>

<style type="text/css" lang="scss">

</style>



